
import { defineComponent, ref } from "vue";

export default defineComponent({
  name: "ScoreForm",
  props: {
    msg: String,
  },
  setup(prop, context) {
    const onclick = () => {
      isDone.value = true;
      console.log(countAll());
    };
    const checkAge = (ev: any) => {
      const num = ev.target.value;
      if (num > 17 && num < 40) {
        showNumber.value = 0;
      } else if (num > 39 && num < 65) {
        showNumber.value = 1;
      } else if (num > 64) {
        showNumber.value = 2;
      } else {
        console.log("err : num = " + num);
      }
      console.log("showNumber = " + showNumber.value);
    };
    const age = ref(18);
    const sex = ref(0);
    const height = ref(160);
    const weight = ref(50);
    const resultMsg = "result message here";
    const isDone = ref(false);
    const showNumber = ref(1);
    class Ill {
      constructor(name: string, id: number, points: Array<number>) {
        this.name = name;
        this.id = id;
        this.points = points;
      }
      name = "";
      id = 0;
      val = 0;
      points: Array<number> = [];
      isShow() {
        return this.points[showNumber.value] != 0;
      }
      getpoint() {
        return this.points[showNumber.value];
      }
    }
    const ills = ref<Array<Ill>>([
      new Ill("うっ血性心不全", 0, [0, 0, 2]),
      new Ill("脳血管疾患", 1, [0, 0, 1]),
      new Ill("糖尿病", 2, [0, 1, 2]),
      new Ill("高血圧", 3, [0, 0, 2]),
      new Ill("悪性疾患", 4, [3, 0, 0]),
      new Ill("発熱", 5, [2, 2, 4]),
      new Ill("咳", 6, [0, 1, 1]),
      new Ill("呼吸困難", 7, [1, 2, 4]),
      new Ill("喘鳴", 8, [1, 0, 0]),
      new Ill("倦怠感", 9, [0, 1, 0]),
    ]);
    const countAll = () => {
      var sum = 0;
      //ills
      ills.value.forEach((element) => {
        sum += element.getpoint() * element.val;
      });
      //sex
      if (sex.value == 1 && age.value < 65) {
        sum += 1;
      }
      //age
      if (
        (age.value > 29 && age.value < 40) ||
        (age.value > 49 && age.value < 60)
      ) {
        sum += 1;
      } else if (age.value > 59 && age.value < 65) {
        sum += 3;
      } else if (age.value > 74) {
        sum += 2;
      }
      //weight, height
      var bmi: number = weight.value / (height.value / 100) ** 2;
      console.log("BMI = " + bmi);
      if (age.value < 40) {
        if (bmi > 30) {
          sum += 2;
        } else if (bmi > 23) {
          sum += 1;
        }
      } else {
        if (bmi > 25) {
          sum += 2;
        }
      }
      //Result
      return sum;
    };
    return {
      onclick,
      checkAge,
      age,
      sex,
      height,
      weight,
      resultMsg,
      isDone,
      ills,
      showNumber,
    };
  },
});
